 .item-card {
  width: auto;
  background-color: #f0c5c5;
  border: 1px solid#f1f2f6;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0.5rem 2rem 0.5rem 0.5rem;
  border-radius: 0.3rem;
  /* margin: 0.7rem; */
  position: relative;
  text-align: center;
}

.red{
  width: auto;
  background-color: #E74C3C;
  border: 1px solid#f1f2f6;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 0.5rem 2rem 0.5rem 0.5rem;
  border-radius: 0.3rem;
  /* margin: 0.7rem; */
  position: relative;
  text-align: center;
}

.green{
  width: auto;
  background-color: #32a852;
  border: 1px solid#f1f2f6;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 0.5rem 2rem 0.5rem 0.5rem;
  border-radius: 0.3rem;
  /* margin: 0.7rem; */
  position: relative;
  text-align: center;
}

.letter{
font-size: "0.9rem";
width: "0rem";
